import * as React from 'react';
import ListItemButton from '@mui/material/ListItemButton';
import ListItemIcon from '@mui/material/ListItemIcon';
import ListItemText from '@mui/material/ListItemText';
import ListItemAvatar from '@mui/material/ListItemAvatar';
import Avatar from '@mui/material/Avatar';
import DashboardIcon from '@mui/icons-material/Dashboard';
import ShoppingCartIcon from '@mui/icons-material/ShoppingCart';
import PeopleIcon from '@mui/icons-material/People';
import BarChartIcon from '@mui/icons-material/BarChart';
import LayersIcon from '@mui/icons-material/Layers';
import Collapse from '@mui/material/Collapse';
import List from '@mui/material/List';
import ExpandLess from '@mui/icons-material/ExpandLess';
import ExpandMore from '@mui/icons-material/ExpandMore';
import Divider from '@mui/material/Divider';
import Box from '@mui/material/Box';
import Inventory from '@mui/icons-material/Inventory'; // Added this line
import Store from '@mui/icons-material/Store'; // Added this line

export const MainListItems = () => {
  const [openOrder, setOpenOrder] = React.useState(false);
  const [openSupplier, setOpenSupplier] = React.useState(false);
  const [openUser, setOpenUser] = React.useState(false);
  const [openInventory, setOpenInventory] = React.useState(false);
  const [openProduct, setOpenProduct] = React.useState(false);


  const handleClickOrder = () => {
    setOpenOrder(!openOrder);
  };

  const handleClickSupplier = () => {
    setOpenSupplier(!openSupplier);
  };

  const handleClickUser = () => {
      setOpenUser(!openUser);
  };

  const handleClickInventory = () => {
        setOpenInventory(!openInventory);
  };

  const handleClickProduct = () => {
        setOpenProduct(!openProduct);
  };

  return (
    <React.Fragment>
      <ListItemButton sx={{ justifyContent: 'flex-start', marginLeft: 5 }}>
        <Box sx={{ display: 'flex', flexDirection: 'column', alignItems: 'center', gap: 2, marginBottom: 5 }}>
          <ListItemAvatar>
            <Avatar
              alt="Admin Name"
              src="/sample/sample1.jpg"
              sx={{ borderRadius: '50%', width: 100, height: 100 }} //  
            />
          </ListItemAvatar>
          <ListItemText primary="" />
        </Box>
      </ListItemButton>
      <Divider />
      <ListItemButton onClick={handleClickOrder}>
        <ListItemIcon>
          <DashboardIcon />
        </ListItemIcon>
        <ListItemText primary=" " />
        {openOrder ? <ExpandLess /> : <ExpandMore />}
      </ListItemButton>
      <Collapse in={openOrder} timeout="auto" unmountOnExit>
        <List component="div" disablePadding>
          <ListItemButton sx={{ pl: 4 }}>
            <ListItemText primary=" " />
          </ListItemButton>
          <ListItemButton sx={{ pl: 4 }}>
            <ListItemText primary=" " />
          </ListItemButton>
          <ListItemButton sx={{ pl: 4 }}>
            <ListItemText primary=" " />
          </ListItemButton>
          <ListItemButton sx={{ pl: 4 }}>
            <ListItemText primary=" " />
          </ListItemButton>
        </List>
      </Collapse>

      <ListItemButton onClick={handleClickSupplier}>
        <ListItemIcon>
          <ShoppingCartIcon />
        </ListItemIcon>
        <ListItemText primary=" " />
        {openSupplier ? <ExpandLess /> : <ExpandMore />}
      </ListItemButton>
      <Collapse in={openSupplier} timeout="auto" unmountOnExit>
        <List component="div" disablePadding>
          <ListItemButton sx={{ pl: 4 }}>
            <ListItemText primary=" " />
          </ListItemButton>
          <ListItemButton sx={{ pl: 4 }}>
            <ListItemText primary=" " />
          </ListItemButton>
          <ListItemButton sx={{ pl: 4 }}>
            <ListItemText primary=" " />
          </ListItemButton>
          <ListItemButton sx={{ pl: 4 }}>
            <ListItemText primary=" " />
          </ListItemButton>
        </List>
      </Collapse>

      <ListItemButton onClick={handleClickUser}>
              <ListItemIcon>
                <PeopleIcon />
              </ListItemIcon>
              <ListItemText primary=" " />
              {openUser ? <ExpandLess /> : <ExpandMore />}
            </ListItemButton>
            <Collapse in={openUser} timeout="auto" unmountOnExit>
              <List component="div" disablePadding>
                <ListItemButton sx={{ pl: 4 }}>
                  <ListItemText primary=" " />
                </ListItemButton>
                <ListItemButton sx={{ pl: 4 }}>
                  <ListItemText primary=" " />
                </ListItemButton>
                <ListItemButton sx={{ pl: 4 }}>
                  <ListItemText primary=" " />
                </ListItemButton>
                <ListItemButton sx={{ pl: 4 }}>
                  <ListItemText primary=" " />
                </ListItemButton>
                <ListItemButton sx={{ pl: 4 }}>
                  <ListItemText primary=" " />
                </ListItemButton>
              </List>
            </Collapse>

      <ListItemButton onClick={handleClickInventory}>
              <ListItemIcon>
                <Inventory />
              </ListItemIcon>
              <ListItemText primary=" " />
              {openInventory ? <ExpandLess /> : <ExpandMore />}
            </ListItemButton>
            <Collapse in={openInventory} timeout="auto" unmountOnExit>
              <List component="div" disablePadding>
                <ListItemButton sx={{ pl: 4 }}>
                  <ListItemText primary="  " />
                </ListItemButton>
                <ListItemButton sx={{ pl: 4 }}>
                  <ListItemText primary="   " />
                </ListItemButton>
                <ListItemButton sx={{ pl: 4 }}>
                  <ListItemText primary="  " />
                </ListItemButton>
                <ListItemButton sx={{ pl: 4 }}>
                  <ListItemText primary="  " />
                </ListItemButton>
              </List>
            </Collapse>

      <ListItemButton onClick={handleClickProduct}>
                    <ListItemIcon>
                      <Store />
                    </ListItemIcon>
                    <ListItemText primary=" " />
                    {openProduct ? <ExpandLess /> : <ExpandMore />}
                  </ListItemButton>
                  <Collapse in={openProduct} timeout="auto" unmountOnExit>
                    <List component="div" disablePadding>
                      <ListItemButton sx={{ pl: 4 }}>
                        <ListItemText primary=" " />
                      </ListItemButton>
                      <ListItemButton sx={{ pl: 4 }}>
                        <ListItemText primary=" " />
                      </ListItemButton>
                      <ListItemButton sx={{ pl: 4 }}>
                        <ListItemText primary=" " />
                      </ListItemButton>
                      <ListItemButton sx={{ pl: 4 }}>
                        <ListItemText primary=" " />
                      </ListItemButton>
                      <ListItemButton sx={{ pl: 4 }}>
                        <ListItemText primary=" " />
                      </ListItemButton>
                    </List>
                  </Collapse>
    </React.Fragment>
  );
};